import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Hello from './panels/Hello';
import Dindex from './panels/Dostopr';
import Other from './panels/Other';

// промышленные
import PromIndex from './panels/prom/Index';
import Surgut from './panels/prom/Surgut';
import Niznevartovsk from './panels/prom/Niznevartovsk';
import Omsk from './panels/prom/Omsk';
import Perm from './panels/prom/Perm';
import Ufa from './panels/prom/Ufa';
import Norilsk from './panels/prom/Norilsk';
import Chelabinsk from './panels/prom/Chelabinsk';
import Novokuznetsk from './panels/prom/Novokuznetsk';

// портовые
import PortIndex from './panels/ports/Index';
import Novorossiysk from './panels/ports/Novorossiysk';
import Primorsk from './panels/ports/Primorsk';
import Nahodka from './panels/ports/Nahodka';
import PeterburgPort from './panels/ports/Peterburg'

// курортные
import KurortIndex from './panels/curorts/Index';

// петербург и москва :)
import Moscow from './panels/prom/Moscow';
import Peterburg from './panels/prom/Peterburg';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState();

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Hello id='hello' go={go} />
			<Dindex id='dostIndex' go={go} />
			<PromIndex id='prom' go={go} />
			<PortIndex id='port' go={go} />
			<KurortIndex id='kur' go={go} />
			<Other id='other' go={go} />
			<Moscow id='moscow' go={go} />
			<Peterburg id='peterburg' go={go} />
			<Surgut id='surgut' go={go} />
			<Niznevartovsk id='niznevartovsk' go={go} />
			<Omsk id='omsk' go={go} />
			<Perm id='perm' go={go} />
			<Ufa id='ufa' go={go} />
			<Norilsk id='norilsk' go={go} />
			<Chelabinsk id='chelabinsk' go={go} />
			<Novokuznetsk id='novokuznetsk' go={go} />

			<Novorossiysk id="novorossiysk" go={go} />
			<Primorsk id="primorsk" go={go} />
			<Nahodka id="nahodka" go={go} />
			<PeterburgPort id="peterburgPort" go={go} />
		</View>
	);
}

export default App;