import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Москва</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-18.userapi.com/c851428/v851428521/1f1146/3E10pUIvuIo.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Москва — столица Российской Федерации, город-герой, центр Московской области.

                Москва — город федерального значения, субъект Российской Федерации. Крупнейший в стране и один из важнейших в мире политический, промышленный, научный и культурный центр. Расположена в Европейской части Российской Федерации, в междуречье Оки и Волги, на реке Москва. Граница города в основном проходит по Московской кольцевой автомобильной дороге.

                Площадь Москвы около 1000 км2. 10 миллионов жителей (по данным переписи 2002 года). В Московский транспортный узел входят 11 железных дорог, три речных порта, 4 аэропорта — Внуково, Домодедово, Шереметьево, Быково. Количество станций метрополитена достигает 161, а длина рельсового пути превышает 220 км.

                Вокруг Москвы сложилась крупнейшая в Российской Федерации городская агломерация.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1147 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;