import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Уфа</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-50.userapi.com/c850736/v850736521/1f1205/ZdMt9LnQElU.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                  Город Уфа - столица многонациональной Республики Башкортостан, была основана в 1574 году, следовательно в Уфе находится большое число достопримечательностей. Уфа расположена на берегу реки Белой, при впадении в неё рек Уфа и Дёмана холмах с живописной природой. С правого берега реки Белой открываются потрясающие по красоте просторы. Живописные изгибы трех рек, которые опоясывают Уфу, поблескивают среди зеленых полей, лугов и лесов, подчеркивая тем самым красоту города «на холмах».
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1574 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область (республика)</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Peterburg;