import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="port"><Icon24BrowserBack /></HeaderButton>}>Новороссийск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-51.userapi.com/c854024/v854024463/132c31/4nrhzdurNdg.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Город Новороссийск — морской курорт, расположенный на юге России, входит в состав Краснодарского края. Население города составляет 251013 человек. Новороссийск является важным транспортным центром, т.к в городе находится крупнейший в России Черноморский порт.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1838 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область (край)</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;