import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Санкт-Петербург</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-55.userapi.com/c856020/v856020521/131d02/oxsIr58CXns.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Санкт-Петербург — прекрасный город на Неве!
                
                Белые ночи, разводные мосты, множество различных исторических памятных мест. Недаром Санкт-Петербург именуют Северной Венецией, и он для жителей России является ее культурной столицей.

                Для многих людей близкие и знакомые слова Невка, Фонтанка и Мойка. Изобилие каналов и ручьев просто потрясает. А ведь есть еще пруды и искусственные водоемы, а также красивые озера. 
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1703 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Peterburg;