import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Челябинск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-2.userapi.com/c858332/v858332179/b79a0/UtpBpe2C4To.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Город Челябинск основан в сентябре 1736г. на реке Миасс, город находится в азиатской части Южного Урала по берегам реки Миасс. Климат в городе умеренно-континентальный, зима продолжается с ноября-март, а лето непродолжительное, но жаркое и дождливое. Площадь города приблизительно пятьдесят тысяч гектаров. В Челябинске расположено семь районов, более 50 поселков и 1300 улиц. Самый новый район – Курчатовский, он образован в 1985г. и получил название в честь знаменитого земляка, академика И.В.Курчатова. Известные личности Челябинска: дореволюционный городской голова Александр Бейвель, знаменитый председатель Челябинского Совета Самуил Цвиллинг, олимпийская чемпионка Лидия Скобликова, музыкант Валерий Ярушин и многие другие горожане. Челябинск является административным центром Челябинский области, столица Южного Урала. Город располагается на расстоянии 210 км южнее Екатеринбурга, а также это крупный промышленный центр, телефонный код города 351, разница во времени с Москвой составляет плюс два часа. В Челябинской области расположено много богатых и разнообразных природных ресурсов.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1736 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;