import React from 'react';
import { Panel, PanelHeader, HeaderButton, Group, Cell, List, Link, Div } from '@vkontakte/vkui';
import Icon20HomeOutline from '@vkontakte/icons/dist/20/home_outline';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const prom_index = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader multiline left={<HeaderButton onClick={go} data-to="dostIndex"><Icon24BrowserBack /></HeaderButton>}>Промышленные города</PanelHeader>
        <Group title="Выберите город">      
        <List>   
            <Cell onClick={go} data-to="moscow" before={<Icon20HomeOutline />} expandable>
                Москва
            </Cell>
            <Cell onClick={go} data-to="peterburg" before={<Icon20HomeOutline />} expandable>
                Санкт-Петербург
            </Cell>
            <Cell onClick={go} data-to="surgut" before={<Icon20HomeOutline />} expandable>
                Сургут
            </Cell>
            <Cell onClick={go} data-to="niznevartovsk" before={<Icon20HomeOutline />} expandable>
               Нижневартовск
            </Cell>
            <Cell onClick={go} data-to="omsk" before={<Icon20HomeOutline />} expandable>
               Омск
            </Cell>
            <Cell onClick={go} data-to="perm" before={<Icon20HomeOutline />} expandable>
               Пермь
            </Cell>
            <Cell onClick={go} data-to="ufa" before={<Icon20HomeOutline />} expandable>
               Уфа
            </Cell>
            <Cell onClick={go} data-to="norilsk" before={<Icon20HomeOutline />} expandable>
               Норильск
            </Cell>
            <Cell onClick={go} data-to="chelabinsk" before={<Icon20HomeOutline />} expandable>
               Челябинск
            </Cell>
            <Cell onClick={go} data-to="novokuznetsk" before={<Icon20HomeOutline />} expandable>
               Новокузнецк
            </Cell>
            <center><Div><Link href="https://vk.me/landmarks" target="_blank">Предложить город</Link></Div></center>
        </List>  
        </Group>
    </Panel>
);

export default prom_index;