import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="port"><Icon24BrowserBack /></HeaderButton>}>Новороссийск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-59.userapi.com/c854024/v854024463/132c45/bqTAmbyafl8.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Приморск расположился на берегу живописного Финского залива в Выборгском районе Ленинградской области. Город расположен в 104 км от Санкт-Петербурга. За период своего длительного существования этот город принадлежал не одному государству. В разные времена им владели шведы, финны, русские. Первоначально о городке стало известно из древних летописей 1268 года. Но и до тех далеких времен Карельский перешеек населяли люди каменного века. Во время раскопок в Приморске были найдены древнейшие орудия труда.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1268 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область (край)</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;