import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Пермь</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell description="Фото города">
                <img style={{borderRadius: "25px", width: "250px", height: "150px"}} src="https://sun9-5.userapi.com/c854016/v854016521/1356cd/1tIhcyIUEh4.jpg"></img>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                    Центр Пермского края, город Пермь находится в 1100 километрах, восточнее Москвы. В городе проживает около одного миллиона жителей. Пермь - третий по площади город России. Площадь города - примерно 800 квадратных километров. Пермь тянется вдоль реки Камы на 70 километров. Среднегодовая температура воздуха в городе: +2,3 С.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1723 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }}>Область (край)</Button>
                <Button size="l" stretched>Достопримечательности</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Peterburg;